.payment-container {
    background-color: #1a202c;
    color: white;
    padding: 20px;
    border-radius: 8px;
    width: 80%;
    margin: 0 auto;
    max-width: 900px;
  }
  
  .payment-heading {
    font-size: 2.5rem;
    color: #48bb78;
    text-align: center;
  }
  
  .payment-subheading {
    font-size: 1.2rem;
    color: #e2e8f0;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .order-summary {
    margin-top: 20px;
  }
  
  .order-summary-heading {
    font-size: 1.8rem;
    color: #48bb78;
    margin-bottom: 10px;
  }
  
  .order-items {
    list-style-type: none;
    padding: 0;
  }
  
  .order-item {
    background-color: #2d3748;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .total-summary {
    margin-top: 20px;
  }
  
  .total-summary div {
    font-size: 1.2rem;
    color: #e2e8f0;
    margin: 5px 0;
  }
  
  .final-total {
    font-size: 1.5rem;
    color: #48bb78;
    font-weight: bold;
    margin-top: 10px;
  }
  
  .payment-details {
    margin-top: 40px;
  }
  
  .payment-details-heading {
    font-size: 1.8rem;
    color: #48bb78;
    margin-bottom: 10px;
  }
  
  .payment-form input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    background-color: #2d3748;
    color: white;
    border: 1px solid #48bb78;
    border-radius: 5px;
  }
  
  .payment-date {
    display: flex;
    justify-content: space-between;
  }
  
  .payment-date div {
    width: 48%;
  }
  
  .payment-submit {
    margin-top: 20px;
    text-align: center;
  }
  
  .pay-button {
    background-color: #48bb78;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 5px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .pay-button:hover {
    background-color: #38a169;
  }
  