/* Cart container styles */
.cart-container {
    padding: 20px;
    background-color: #1a202c;
    min-height: 100vh;
    color: #fff;
  }
  
  .cart-container h2 {
    text-align: center;
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .cart-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #2d3748;
    margin-bottom: 15px;
    border-radius: 8px;
  }
  
  .cart-item img {
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .cart-item-details {
    flex: 1;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
  }
  
  .cart-item-details span {
    margin: 3px 0;
  }
  
  .cart-item-details button {
    background-color: #38a169;
    border: none;
    padding: 5px 15px;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    margin: 3px;
  }
  
  .cart-item-details button:hover {
    background-color: #2f855a;
  }
  
  .cart-summary {
    background-color: #2d3748;
    padding: 20px;
    border-radius: 8px;
    margin-top: 20px;
  }
  
  .cart-summary h3 {
    margin-bottom: 10px;
  }
  
  .cart-summary div {
    display: flex;
    justify-content: space-between;
  }
  
  .checkout-btn {
    background-color: #38a169;
    padding: 15px 30px;
    border-radius: 8px;
    font-size: 1.2rem;
    margin-top: 20px;
    cursor: pointer;
    color: white;
    width: 100%;
  }
  
  .checkout-btn:hover {
    background-color: #2f855a;
  }
  
  /* Make sure to add the .cart-summary, .checkout-btn, etc., in your component */
  