/* General Styling */
body {
    background: linear-gradient(135deg, #0d0d0d, #1a1a1a);
    color: white;
    font-family: 'Poppins', sans-serif;
  }
  
  /* Popup Styling */
  .popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0; /* Start hidden */
    pointer-events: none; /* Disable interaction */
    transition: opacity 0.4s ease, pointer-events 0.4s ease; /* Smooth fade-in */
  }
  
  .popup.active {
    opacity: 1; /* Show when active */
    pointer-events: auto; /* Enable interaction */
  }
  
  .popup-content {
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(10px);
    padding: 30px;
    border-radius: 15px;
    text-align: center;
    width: 500px;
    max-height: 80vh; /* Limit height of popup */
    overflow-y: auto; /* Enable scrolling */
    box-shadow: 0px 10px 20px rgba(255, 69, 0, 0.3);
    transform: translateY(-20px); /* Start off-screen */
    animation: slideUp 0.4s ease-out forwards; /* Smooth slide-in animation */
  }
  
  .popup-content h2 {
    font-size: 2rem;
    color: #ffcc00;
    margin-bottom: 20px;
  }
  
  .popup-content ul {
    list-style: none;
    padding: 0;
    text-align: left;
  }
  
  .popup-content ul li {
    font-size: 1.2rem;
    color: #fff;
    margin: 10px 0;
  }
  
  .close-btn {
    background: linear-gradient(45deg, #ff4d4d, #ffcc00);
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    transition: background 0.3s ease-in-out;
  }
  
  .close-btn:hover {
    background: linear-gradient(45deg, #ffcc00, #ff4d4d);
  }
  
  /* Animations */
  @keyframes slideUp {
    from {
      transform: translateY(20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  /* Weekly Plan Styling */
  .weekly-plan {
    text-align: left;
    margin-top: 20px;
  }
  
  .day-card {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    padding: 15px;
    margin: 10px 0;
  }
  
  .day-card h3 {
    font-size: 1.5rem;
    color: #ff4d4d;
    margin-bottom: 10px;
  }
  
  .day-card ul {
    list-style-type: none;
    padding-left: 0;
  }
  
  .day-card li {
    font-size: 1.2rem;
    color: #ddd;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .popup-content {
      width: 90%;
    }
  }
  