/* General Page Styling */
.diet-plans {
    text-align: center;
    padding: 50px;
    background: linear-gradient(to bottom, #f0f0f0, #d6e4c4);
    min-height: 100vh;
    font-family: 'Arial', sans-serif;
  }
  
  .diet-plans h1 {
    font-size: 2.8rem;
    color: #2d572c;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .diet-plans p {
    font-size: 1.3rem;
    color: #444;
    margin-bottom: 30px;
  }
  
  /* Diet Cards */
  .diet-cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
  }
  
  .diet-card {
    background: white;
    padding: 25px;
    border-radius: 12px;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
    width: 300px;
    text-align: center;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  }
  
  .diet-card:hover {
    transform: translateY(-5px);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  .diet-card h2 {
    font-size: 1.7rem;
    color: #2d572c;
    margin-bottom: 10px;
  }
  
  .diet-card p {
    font-size: 1.1rem;
    color: #666;
    margin-bottom: 15px;
  }
  
  .diet-card button {
    background-color: #4CAF50;
    color: white;
    padding: 12px 18px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem;
    transition: background 0.3s ease-in-out;
  }
  
  .diet-card button:hover {
    background-color: #3c8c40;
  }
  
  /* Popup Styling */
  .popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.4s ease, pointer-events 0.4s ease;
    z-index: 1000;
  }
  
  .popup.active {
    opacity: 1;
    pointer-events: auto;
  }
  
  .popup-content {
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(10px);
    padding: 30px;
    border-radius: 15px;
    text-align: center;
    width: 400px;
    max-height: 80vh;
    overflow-y: auto;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
    transform: translateY(-20px);
    animation: slideUp 0.4s ease-out forwards;
  }
  
  .popup-content h2 {
    font-size: 2rem;
    color: #ffcc00;
    margin-bottom: 20px;
  }
  
  .popup-content ul {
    list-style: none;
    padding: 0;
    text-align: left;
  }
  
  .popup-content ul li {
    font-size: 1.2rem;
    color: #fff;
    margin: 10px 0;
  }
  
  .close-btn {
    background: linear-gradient(45deg, #ff4d4d, #ffcc00);
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    transition: background 0.3s ease-in-out;
    margin-top: 20px;
  }
  
  .close-btn:hover {
    background: linear-gradient(45deg, #ffcc00, #ff4d4d);
  }
  
  /* Animations */
  @keyframes slideUp {
    from {
      transform: translateY(20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .popup-content {
      width: 90%;
    }
  }
  