/* Custom Styles for Merchandise Page */

/* General Styles */
body {
    font-family: 'Arial', sans-serif;
    background-color: #1a202c; /* Dark background for overall page */
    color: #f7fafc; /* Light text color for good contrast */
  }
  
  h1 {
    font-family: 'Roboto', sans-serif;
    color: #68d391; /* Green color for title */
  }
  
  /* Product Cards */
  .product-card {
    background-color: #2d3748; /* Dark card background */
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 16px;
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .product-card:hover {
    transform: scale(1.05); /* Slightly enlarge on hover */
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.15); /* Slight shadow increase */
  }
  
  /* Product Image */
  .product-card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Subtle shadow on the image */
  }
  
  /* Product Name */
  .product-card h2 {
    color: #48bb78; /* Lighter green for the product name */
    font-size: 1.25rem;
    font-weight: 600;
    margin-top: 16px;
    text-transform: uppercase;
  }
  
  /* Price */
  .product-card p {
    color: #edf2f7; /* Light gray for price text */
    font-size: 1.125rem;
    font-weight: 700;
    margin-top: 8px;
  }
  
  /* Button */
  .product-card button {
    background-color: #48bb78; /* Green background for the button */
    color: #ffffff;
    padding: 12px;
    font-size: 1.125rem;
    font-weight: 600;
    border-radius: 8px;
    width: 100%;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  .product-card button:hover {
    background-color: #38a169; /* Darker green when hovered */
    transform: scale(1.05); /* Slight button size increase */
  }
  
  /* Back to Home Link */
  .back-to-home {
    color: #68d391; /* Green color for the link */
    font-weight: 700;
    font-size: 1.25rem;
    text-decoration: underline;
    transition: color 0.3s ease;
  }
  
  .back-to-home:hover {
    color: #48bb78; /* Darker green on hover */
  }
  
  /* Responsive Grid */
  @media screen and (max-width: 640px) {
    .grid {
      grid-template-columns: 1fr; /* Stack items vertically on small screens */
    }
  }
  
  @media screen and (min-width: 640px) and (max-width: 1024px) {
    .grid {
      grid-template-columns: repeat(2, 1fr); /* Two-column grid for medium screens */
    }
  }
  
  /* Cart Page Styles */
  
  /* Container for the cart page */
  .cart-container {
    background-color: #1a202c;
    padding: 20px;
    min-height: 100vh;
  }
  
  /* Cart items */
  .cart-item {
    background-color: #2d3748;
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .cart-item h2 {
    color: #48bb78;
    font-size: 1.25rem;
  }
  
  .cart-item p {
    color: #edf2f7;
  }
  
  .cart-item button {
    background-color: #f56565; /* Red button for removing items */
    color: #ffffff;
    padding: 8px 12px;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .cart-item button:hover {
    background-color: #e53e3e;
  }
  
  /* Coupon input and button */
  .coupon-section {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .coupon-input {
    padding: 10px;
    border-radius: 6px;
    width: 60%;
    margin-right: 10px;
    border: 1px solid #2d3748;
    background-color: #edf2f7;
    color: #1a202c;
  }
  
  .apply-coupon-btn {
    background-color: #48bb78;
    padding: 10px 20px;
    border-radius: 6px;
    color: #ffffff;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .apply-coupon-btn:hover {
    background-color: #38a169;
  }
  
  /* Total and Proceed to Payment */
  .total-section {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    font-size: 1.25rem;
  }
  
  .total-section p {
    color: #edf2f7;
  }
  
  .proceed-btn {
    background-color: #48bb78;
    padding: 12px 20px;
    border-radius: 8px;
    color: #ffffff;
    font-size: 1.25rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .proceed-btn:hover {
    background-color: #38a169;
  }
  