/* General Styles for ExpertAdvice Page */
.expert-advice {
    background: linear-gradient(145deg, #f1f1f1, #dcdcdc);
    padding: 40px;
    font-family: 'Roboto', sans-serif;
  }
  
  .header {
    text-align: center;
    margin-bottom: 50px;
  }
  
  .headline {
    font-size: 2.5rem;
    font-weight: bold;
    color: #2c3e50;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: transform 0.5s ease-in-out;
  }
  
  .header p {
    font-size: 1.1rem;
    color: #7f8c8d;
    margin-top: 10px;
  }
  
  /* Advice Cards Section */
  .advice-section {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-top: 30px;
  }
  
  .advice-card {
    background: #fff;
    border-radius: 15px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: center;
    width: 30%;
    opacity: 0;
    transform: translateY(50px);
    transition: transform 0.3s ease, opacity 0.3s ease;
  }
  
  .advice-card h3 {
    font-size: 1.5rem;
    color: #2980b9;
    font-weight: 600;
  }
  
  .advice-card p {
    font-size: 1rem;
    color: #34495e;
    line-height: 1.6;
    margin-top: 15px;
  }
  
  .advice-card ul {
    list-style-type: none;
    padding: 0;
    margin-top: 15px;
  }
  
  .advice-card li {
    margin: 10px 0;
  }
  
  .advice-card li strong {
    color: #16a085;
  }
  
  .advice-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 6px 30px rgba(0, 0, 0, 0.1);
  }
  
  .advice-card:hover h3 {
    color: #16a085;
  }
  
  .expert-resources {
    margin-top: 50px;
    text-align: center;
  }
  
  .expert-resources h3 {
    font-size: 1.8rem;
    color: #2c3e50;
    font-weight: 700;
    margin-bottom: 20px;
  }
  
  .expert-resources ul {
    list-style-type: none;
    padding: 0;
  }
  
  .expert-resources li {
    margin: 10px 0;
  }
  
  .expert-resources a {
    text-decoration: none;
    color: #2980b9;
    font-size: 1.1rem;
    transition: color 0.3s ease;
  }
  
  .expert-resources a:hover {
    color: #16a085;
  }
  
  @media (max-width: 768px) {
    .advice-section {
      flex-direction: column;
      align-items: center;
    }
  
    .advice-card {
      width: 80%;
      margin-bottom: 20px;
    }
  }
  